import React, { useState, useEffect} from 'react'
import { auth, db, storage } from "../src/firebase/fbConfig"
import Header from "../src/Header/header"
import "../src/App.css"
import "../src/css/header.css"
import Posts from "../src/Posts/Posts"
import Post from "../src/Posts/Post"
import ImageUpload from "../src/Posts/ImageUpload"
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import { Button, input } from "@material-ui/core"
import SigninModal from "../src/Auth pages/signinModal"
import SignupModal from "../src/Auth pages/signupModal"

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}
const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

function App() {
  const classes = useStyles();
  const [user_posts, setUser_posts] = useState([]);
  const [open, setOpen] = useState(false)
  const [modalStyle] = useState(getModalStyle);
  const [email, setEmail] =useState("")
  const [password, setPassword] =useState("")
  const [username, setUsername] = useState("")
  const [user, setUser] = useState(null)
  const [openSignin, setOpenSignin] = useState(false)

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((authUser) => {
      if (authUser){
        // user has logged in
        console.log(authUser)
        setUser(authUser);
        if (authUser.displayName){
          // dont update user
        }
        else {
          // if user is just created update user
          return authUser.updateProfile({
            displayName: username
          })
        }
      }else{
        // user has logged out
        setUser(null)
      }
    })

    return () => {
      // perform some cleanUp actions
      unsubscribe();
    }
  }, [user, username])
  
  return (
    <div className="app">
      <SigninModal
                  email={email} password={password} 
                  getModalStyle={getModalStyle} modalStyle={modalStyle} 
                  useStyles={useStyles} setOpenSignin={setOpenSignin} 
                  openSignin={openSignin} username={username} setUsername={setUsername}
                  password={password} setPassword={setPassword}/>
      <SignupModal modalStyle={modalStyle} email={email} 
                  password={password} 
                  getModalStyle={getModalStyle} 
                  useStyles={useStyles} setOpen={setOpen} open={open}
                  username={username} setUsername={setUsername}
                  password={password} setPassword={setPassword}/>
      <Header setOpenSignin={setOpenSignin} setOpen={setOpen} user={user}/> 
      <Posts />

{
        user?.displayName ? (
          <ImageUpload username={user.displayName}/>
        ):
        (
          <h2>Sorry you need to login</h2>
        )
      }
    </div>
  )
}

export default App
