import React, { useState, useEffect } from 'react'
import Post from "./Post"
import img1 from "../images/img1.jpg"
import img2 from "../images/img2.jpg"
import img3 from "../images/img3.jpg"
import { db } from "../firebase/fbConfig"
import "../css/post.css"


function Posts() {

    const [user_posts, setUser_posts] = useState([]);


    useEffect(() => {
        db.collection("user_posts").orderBy("timestamp", "desc").onSnapshot(snapshot =>{
          setUser_posts(snapshot.docs.map( doc => ({
            id: doc.id,
            post: doc.data()
          })
            ));
        })
      }, [])

    return(
        <div className="app_post">
            {
                user_posts.map(({id, post}) => (
                    <Post key={id} postId={id} username={post.username} imgURL={post.imgURL} caption={post.caption}/>
                ))
            }

        </div>
    )
}
export default Posts
