import React, {useState, useEffect}  from 'react'
import "../css/post.css"
import { db } from "../firebase/fbConfig"
import { Button, input } from "@material-ui/core"


function Post({username, imgURL, caption, postId}) {
    const [comments, setComments] = useState("")
    const [comment, setComment] = useState("")

    useEffect(() =>{
        let unsubscribe;
        if(postId){
            unsubscribe = db
            .collection("user_posts")
            .doc(postId)
            .collection("comments")
            .onSnapshot((snapshot) => {
                    setComments(snapshot.docs.map((doc) => doc.data([])
                ))

            });
        }
        return () => {
            unsubscribe();
        }
    }, [postId]);

    const postComment =(e) => {
        
    }
    return (
        <div className="post-container">
            <div className="post-header">
                <div className="header-avatar">R</div>
                <div className="post-details">
                    <h3>{username}</h3>
                    <h6>user location</h6>
                </div>
            </div>
            <div className="post-image">
                <img  className="post-url" src={imgURL} alt="paul"/>                    
            </div>
            {/* <div className="post-reactions">

            </div> */}
            <div className="post-caption">
                <h5><strong>{username}</strong>{caption}</h5>
                <div className="post_comments">
                {comments.map((comment) => (
                        <p>
                            <strong>{comment.username}</strong>{comment.text}
                        </p>
                ))}
                </div>
                
                <form className="post_comment">
                    <input type="text"
                            className="post_input"
                            placeholder="Add a comment..."
                            value={comment}
                            onChange={(e) => setComment(e.target.value)}/>
                            <Button className="post_btn"
                                    disabled={!comment}
                                    type="submit"
                                    onClick={postComment}>Post</Button>
                </form>
            </div>
        </div>
    )
}

export default Post
